var server = window.location.hostname;
var port = "1337";
var Graph = /** @class */ (function () {
    function Graph(vector, id_graph) {
        this.vector = vector;
        this.id_graph = id_graph;
    }
    return Graph;
}());
var button = document.getElementById('search_click');
var status_request = document.getElementById("status_request");
function DeleteGraphInfo() {
    var inputValue = document.getElementById("graph").value;
    fetch("http://" + server + ":" + port + "/graph/?id=" + inputValue, { method: "DELETE" })
        .then(function (response) {
        return response.json().then(function (data) { return ({ status: response.status, body: data }); });
    }).then(function (data) {
        if (data.status == 204) {
            status_request.innerHTML = "successfully";
        }
        else if (data.status == 404) {
            status_request.innerHTML = "Not Found";
        }
    });
}
button.addEventListener('click', DeleteGraphInfo);
